import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { retrieveData } from '../redux/modules/data';
import { tabSelector } from '../redux/selectors/frontend';
import { isLoadingSelector } from '../redux/selectors/data';
import Variety from './Variety';
import Agronomy from './Agronomy';
import Mechanization from './Mechanization';
import Disease from './Disease';
import Map from './Map';
import CountryMarkers from './Variety/CountryMarkers';
import LocationMarkers from './Variety/LocationMarkers';
import AgronomyMarkers from './Agronomy/AgronomyMarkers';
import MechanizationMarkers from './Mechanization/MechanizationMarkers';
import DiseaseMarkers from './Disease/DiseaseMarkers';

const App = () => {
  const dispatch = useDispatch();
  const isLoading = useSelector(isLoadingSelector);
  const selectedTab = useSelector(tabSelector);

  useEffect(() => {
    dispatch(retrieveData());
  }, [dispatch]);

  const renderSelectedTab = () => {
    switch (selectedTab.id) {
      case 'variety':
        return <Variety />;
      case 'agronomy':
        return <Agronomy />;
      case 'mechanization':
        return <Mechanization />;
      case 'disease':
        return <Disease />;
      default:
        return null;
    }
  };

  const renderMarkers = () => {
    switch (selectedTab.id) {
      case 'variety':
        return (
          <>
            <CountryMarkers />
            <LocationMarkers />
          </>
        );
      case 'agronomy':
        return <AgronomyMarkers />;
      case 'mechanization':
        return <MechanizationMarkers />;
      case 'disease':
        return <DiseaseMarkers />;
      default:
        return null;
    }
  };

  return (
    <div className="App">
      <Map>{renderMarkers()}</Map>
      {isLoading ? null : renderSelectedTab()}
    </div>
  );
};

export default App;
