import React, { useMemo } from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import { useSelector, useDispatch } from 'react-redux';
import { useTableStyles } from '../Variety/Tables/CustomTable';
import CustomSelect from '../Variety/Tables/CustomSelect';
import { dataSelector } from '../../redux/selectors/data';
import { updateSelectedSeason } from '../../redux/modules/frontend';
import { selectedCountrySelector, selectedLocationSelector, selectedSeasonSelector } from '../../redux/selectors/frontend';

const Disease = ({ state, setState }) => {
  const classes = useTableStyles();
  const dispatch = useDispatch();
  const data = useSelector(dataSelector);
  const selectedLocation = useSelector(selectedLocationSelector);
  const selectedSeason = useSelector(selectedSeasonSelector);

  let seasons = useMemo(
    () => data.diseaseMarkers.map(r => r.properties.season).filter((item, index, self) => self.indexOf(item) === index),
    [data.diseaseMarkers],
  );
  seasons = [...seasons, 'all'];

  if (selectedSeason === null) {
    dispatch(updateSelectedSeason(seasons[0]));
  }

  const dataInCountry = data.diseaseMarkers.filter(r => selectedLocation && r.properties.location === selectedLocation.properties.location);
  return (
    <div>
      <div>
        <span> View Season: </span>
        <CustomSelect items={seasons} onChange={e => dispatch(updateSelectedSeason(e))} selectedItem={selectedSeason} />
      </div>
      <TableContainer>
        <Table className={classes.table}>
          <TableHead>
            <TableRow className={classes.HeaderRow}>
              <TableCell className={classes.Header}>Location</TableCell>
              <TableCell className={classes.Header}>Season</TableCell>
              <TableCell className={classes.Header}>Incidence</TableCell>
              <TableCell className={classes.Header}>Severity</TableCell>
              <TableCell className={classes.Header}>Maximum</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {dataInCountry.map(r => {
              let cells = (
                <>
                  <TableCell className={classes.TableCell}> {r.properties.location} </TableCell>
                  <TableCell className={classes.TableCell}> {r.properties.season} </TableCell>
                  <TableCell className={classes.TableCell}> {r.properties.incidence} </TableCell>
                  <TableCell className={classes.TableCell}> {r.properties.severity} </TableCell>
                  <TableCell className={classes.TableCell}> {r.properties.maximum} </TableCell>
                </>
              );
              let seled = selectedLocation ? selectedLocation.properties.id === r.properties.id : null;
              return (
                <TableRow key={r.properties.id} className={(seled && classes.selectedRow) || undefined}>
                  {cells}
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default Disease;
