import { initialFrontendState } from '../state/frontend';

const UPDATE_SELECTED_TAB = 'frontend/update_selected_tab';
const UPDATE_SELECTED_COUNTRY = 'frontend/update_selected_country';
const UPDATE_SELECTED_LOCATION = 'frontend/update_selected_location';
const UPDATE_SELECTED_SEASON = 'frontend/update_selected_season';

export function updateSelectedLocation(location) {
  return {
    type: UPDATE_SELECTED_LOCATION,
    location,
  };
}

export function updateSelectedCountry(country) {
  return {
    type: UPDATE_SELECTED_COUNTRY,
    country,
  };
}

export function updateSelectedTab(id) {
  return {
    type: UPDATE_SELECTED_TAB,
    id,
  };
}
export function updateSelectedSeason(season) {
  return {
    type: UPDATE_SELECTED_SEASON,
    season,
  };
}

// Reducer
export default function reducer(state = initialFrontendState, action) {
  switch (action.type) {
    case UPDATE_SELECTED_TAB:
      return {
        ...state,
        tabs: state.tabs.map(tab => ({
          ...tab,
          isSelected: tab.id === action.id,
        })),
      };
    case UPDATE_SELECTED_COUNTRY:
      return {
        ...state,
        selectedCountry: action.country,
      };
    case UPDATE_SELECTED_LOCATION:
      return {
        ...state,
        selectedLocation: action.location,
      };
    case UPDATE_SELECTED_SEASON:
      return {
        ...state,
        selectedSeason: action.season,
      };
    default:
      return state;
  }
}
