import { createSelector } from 'reselect';

export const tabsSelector = state => state.frontend.tabs;

export const tabSelector = createSelector([tabsSelector], tabs => {
  return tabs.find(tab => tab.isSelected);
});

export const selectedCountrySelector = state => state.frontend.selectedCountry;
export const selectedLocationSelector = state => state.frontend.selectedLocation;
export const selectedSeasonSelector = state => state.frontend.selectedSeason;
