import React from 'react';
import { Marker } from 'react-map-gl';
import { useDispatch, useSelector } from 'react-redux';
import { ICON, SIZE } from '../../../constants';
import { dataSelector } from '../../../redux/selectors/data';
import { updateSelectedLocation, updateSelectedCountry } from '../../../redux/modules/frontend';
import { selectedLocationSelector, selectedSeasonSelector } from '../../../redux/selectors/frontend';

const DiseaseMarkers = () => {
  const dispatch = useDispatch();
  const activeLocation = useSelector(selectedLocationSelector);
  const data = useSelector(dataSelector);
  const selectedSeason = useSelector(selectedSeasonSelector);
  const diseaseMarkers = data.diseaseMarkers;
  const severityColors = [
    'rgb(44, 130, 201)',
    'rgb(44, 201, 144)',
    'rgb(238, 230, 87)',
    'rgb(252, 185, 65)',
    'rgb(252, 96, 66)',
    'rgb(255, 0, 0)',
  ];

  const onMarkerClick = location => () => {
    dispatch(updateSelectedCountry(data.countryMarkers.find(c => c.properties.country === location.properties.country)));
    dispatch(updateSelectedLocation(location));
  };

  return diseaseMarkers.map(location => {
    const [longitude, latitude] = location.geometry.coordinates;
    const { id, severity, season } = location.properties;
    let display = 'initial';
    if (selectedSeason !== 'all' && season !== selectedSeason) {
      display = 'none';
    }
    return (
      <Marker key={`disease-marker-${id}`} longitude={longitude} latitude={latitude}>
        <svg
          height={SIZE}
          viewBox="0 0 24 24"
          style={{
            display: display,
            cursor: 'pointer',
            fill: severityColors[severity] || 'black',
            stroke: activeLocation && location.properties.id === activeLocation.properties.id ? 'white' : 'none',
            strokeWidth: '3px',
            transform: `translate(${-SIZE / 2}px,${-SIZE}px)`,
          }}
          onClick={onMarkerClick(location)}
        >
          <path d={ICON} />
        </svg>
      </Marker>
    );
  });
};

export default DiseaseMarkers;
