import * as React from 'react';
import MapGL, { FlyToInterpolator, WebMercatorViewport } from 'react-map-gl';
import { Component } from 'react';
import HomeIcon from '@material-ui/icons/Home';
import Message from './Message';
import { connect } from 'react-redux';
import { updateSelectedTab } from '../../redux/modules/frontend';
import { selectedCountrySelector, tabsSelector } from '../../redux/selectors/frontend';
import { OVERALL_BOUNDS, DEFAULT_VIEWPORT, IMAGE_LOOKUP } from '../../constants';
import { updateSelectedLocation, updateSelectedCountry } from '../../redux/modules/frontend';

class Map extends Component {
  constructor(props) {
    super(props);
    this.state = {
      messageClass: 'visible',
      mapWidth: 900,
      mapHeight: 500,
      viewport: DEFAULT_VIEWPORT,
      selectedCountry: null,
    };
    this.mapRef = React.createRef();
    this.goToDefaultViewport = this.goToDefaultViewport.bind(this);
  }

  componentDidMount() {
    const { offsetWidth, offsetHeight } = this.mapRef.current;
    this.setState({
      mapWidth: offsetWidth,
      mapHeight: offsetHeight,
    });
    this.goToDefaultViewport();
    setTimeout(() => {
      setTimeout(() => {
        this.setState({
          messageClass: 'hidden',
        });
      }, 1000);
      this.setState({
        messageClass: 'exiting',
      });
    }, 5000);
  }

  goToDefaultViewport() {
    const viewport = new WebMercatorViewport({
      width: this.state.mapWidth,
      height: this.state.mapHeight,
    }).fitBounds(OVERALL_BOUNDS, { padding: 25 });

    this.props.clearSelectedCountry();
    this.props.clearSelectedLocation();
    this.setState({
      viewport: {
        ...viewport,
        transitionInterpolator: new FlyToInterpolator({ speed: 1.2 }),
      },
    });
  }

  static getDerivedStateFromProps(props, state) {
    if (props.selectedCountry && props.selectedCountry !== state.selectedCountry) {
      let { longitude, latitude } = new WebMercatorViewport({
        width: state.mapWidth,
        height: state.mapHeight,
      }).fitBounds(props.selectedCountry.bbox, { padding: 25 });
      return {
        ...state,
        viewport: {
          longitude,
          latitude,
          zoom: 6,
          transitionInterpolator: new FlyToInterpolator({ speed: 1.2 }),
          transitionDuration: 'auto',
        },
        selectedCountry: props.selectedCountry,
      };
    }
    return {
      ...state,
      selectedCountry: props.selectedCountry,
    };
  }
  render() {
    const { tabs, children, onTabClick } = this.props;
    const { messageClass } = this.state;

    return (
      <div id="map" ref={this.mapRef}>
        {messageClass !== 'hidden' && <Message messageClass={messageClass} />}
        <div className="toggle__button__container">
          {tabs.map(tab => {
            const imageUrls = IMAGE_LOOKUP.get(tab.id);
            const imageUrl = tab.isSelected ? imageUrls[1] : imageUrls[0];
            return (
              <img
                className="icon"
                key={`tab-button-for-${tab.id}`}
                onClick={() => {
                  this.goToDefaultViewport();
                  onTabClick(tab.id);
                }}
                src={imageUrl}
                alt={`Toggle to the ${tab.id} view`}
              />
            );
          })}
        </div>
        <div className="icon__container">
          <HomeIcon className="icon" fontSize="default" onClick={() => this.goToDefaultViewport()} />
        </div>
        <MapGL
          {...this.state.viewport}
          width="100%"
          height="100%"
          mapStyle="mapbox://styles/mapbox/light-v9"
          onViewportChange={v => this.setState({ viewport: v })}
          dragRotate={false}
          mapboxApiAccessToken="pk.eyJ1IjoiZ3JhcGhpY2FjeSIsImEiOiJja2IxMnpiMG0wZzl2MnptcGQ2bGpldGZ1In0.h0CLJxaPaDwWJosU-3TH0w"
        >
          {children}
        </MapGL>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    tabs: tabsSelector(state),
    selectedCountry: selectedCountrySelector(state),
  };
}

const mapDispatchToProps = dispatch => {
  return {
    clearSelectedCountry: () => dispatch(updateSelectedCountry(null)),
    clearSelectedLocation: () => dispatch(updateSelectedLocation(null)),
    onTabClick: id => {
      dispatch(updateSelectedTab(id));
      dispatch(updateSelectedLocation(null));
      dispatch(updateSelectedCountry(null));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Map);
