import React from 'react';
import { Marker } from 'react-map-gl';
import { useDispatch, useSelector } from 'react-redux';
import { ICON, SIZE } from '../../../constants';
import { dataSelector } from '../../../redux/selectors/data';
import { selectedCountrySelector, selectedLocationSelector } from '../../../redux/selectors/frontend';
import { updateSelectedLocation } from '../../../redux/modules/frontend';

const LocationMarkers = () => {
  const dispatch = useDispatch();
  const data = useSelector(dataSelector);
  const selectedCountry = useSelector(selectedCountrySelector);
  const activeLocation = useSelector(selectedLocationSelector);

  const locationMarkers = selectedCountry ? data.locationMarkers[selectedCountry.properties.id] : [];
  const countryColors = data.countryColors;

  const onMarkerClick = location => () => {
    // TODO update the dropdownidx and such
    dispatch(updateSelectedLocation(location));
  };

  return locationMarkers.map(location => {
    const [longitude, latitude] = location.geometry.coordinates;
    const { id, country } = location.properties;
    return (
      <Marker key={`location-marker-${id}`} longitude={longitude} latitude={latitude}>
        <svg
          height={SIZE}
          viewBox="0 0 24 24"
          style={{
            cursor: 'pointer',
            fill: countryColors.get(country),
            stroke: activeLocation && location.properties.id === activeLocation.properties.id ? 'white' : 'none',
            strokeWidth: '3px',
            transform: `translate(${-SIZE / 2}px,${-SIZE}px)`,
          }}
          onClick={onMarkerClick(location)}
        >
          <path d={ICON} />
        </svg>
      </Marker>
    );
  });
};

export default LocationMarkers;
